function getClientRect (el: HTMLElement) {
	try {
		const offsetRight: number = Math.abs((el.offsetParent as HTMLElement).offsetHeight - el.offsetHeight - el.offsetTop)
		const offsetBottom: number = Math.abs((el.offsetParent as HTMLElement).offsetWidth - el.offsetWidth - el.offsetLeft)
		return {
			right: offsetRight,
			bottom: offsetBottom
		}
	} catch (error) {
		console.log(error)
		return { right: 0, bottom: 0 }
	}
}

export default getClientRect
