import React from 'react'

import styles from './styles/_separator.module.scss'

declare interface ClientRect { }

function Separator (props: {offsetBottom: number, offsetRight: number}) { 
	const { offsetBottom, offsetRight } = props
	const positionCSS: object = {
		right: offsetRight,
		bottom: offsetBottom
	}
	return (
		<div className={styles.separator} style={{ ...positionCSS }}></div>
	)
}

export default Separator
